.derecha
{
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet 
    { 
        align-items: flex-end; 
    }
}

.navegacion
{
    visibility: hidden;
    opacity: 0;
    transition: visibility .2s ease-out, opacity .2s ease-out;

    a
    {
        color: white;
        font-size: 1.8rem;
        text-align: center;
        text-decoration: none;
        margin: 0.5rem 0rem;
        padding: 0rem 1rem;
        border-radius: 5px;
        transition: background-color .1s ease-in-out;


        // Default mobile settings
        display: block; // Los enlaces se muestran uno debajo del otro
        justify-items: center;
        
        &:hover
        {
            color: $verde;
            background-color: rgba(255, 255, 255, .2);
            transition: background-color .1s ease-in-out;
        }
    }

    // Tablets
    @include tablet
    {
        display: flex;
        visibility: visible;
        opacity: 1;
        gap: 1rem;
    }
}


// Menu hamburguesa
.mobile-menu
{
    
    @include tablet{ display: none; }

    img
    {
        width: 6rem;
        margin-top: 1rem;
        padding: 0 .7rem;
        border-radius: 5px;
        transition: background-color 0.1s ease-in;
        
        &:hover
        {
            background-color: rgba(255, 255, 255, .1);
            transition: background-color 0.1s ease-in;
        }
    }
}
.is-shown
{
    visibility: visible;
    opacity: 1;
}

.dark-mode-btn
{
    width: 3rem;
    margin: 1rem;
    filter: invert(100%);
    transition: filter .1s ease-out;

    &:hover
    {
        filter: invert(0%);
        cursor: pointer;
        transition: filter .1s ease-in;
    }
}