.no-webp .index {
  background-image: url("../img/header.jpg"); }

.webp .index {
  background-image: url("../img/header.webp"); }

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0; }
  .header.index {
    background-position: center center;
    background-size: cover; }
    @media (min-width: 768px) {
      .header.index {
        min-height: 70rem; } }
    @media (min-width: 768px) {
      .header.index .header-contenido {
        min-height: 70rem; }
        .header.index .header-contenido h1 {
          max-width: 60rem; } }
    .header.index .header-contenido h1 {
      margin: 1rem;
      color: white;
      font-weight: bold;
      text-align: left; }

.header-contenido {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

.header-nav {
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media (min-width: 768px) {
    .header-nav {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-end; } }

.footer {
  margin-top: 5rem;
  background-color: #333333;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem; }
  .footer p {
    margin: 1rem;
    color: white; }
  @media (min-width: 768px) {
    .footer {
      padding: 1rem 5rem;
      flex-direction: row-reverse;
      justify-content: space-between; } }
  .footer .navegacion {
    visibility: visible;
    opacity: 1; }

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media (min-width: 768px) {
    .derecha {
      align-items: flex-end; } }

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility .2s ease-out, opacity .2s ease-out; }
  .navegacion a {
    color: white;
    font-size: 1.8rem;
    text-align: center;
    text-decoration: none;
    margin: 0.5rem 0rem;
    padding: 0rem 1rem;
    border-radius: 5px;
    transition: background-color .1s ease-in-out;
    display: block;
    justify-items: center; }
    .navegacion a:hover {
      color: #71B100;
      background-color: rgba(255, 255, 255, 0.2);
      transition: background-color .1s ease-in-out; }
  @media (min-width: 768px) {
    .navegacion {
      display: flex;
      visibility: visible;
      opacity: 1;
      gap: 1rem; } }

@media (min-width: 768px) {
  .mobile-menu {
    display: none; } }

.mobile-menu img {
  width: 6rem;
  margin-top: 1rem;
  padding: 0 .7rem;
  border-radius: 5px;
  transition: background-color 0.1s ease-in; }
  .mobile-menu img:hover {
    background-color: rgba(255, 255, 255, 0.1);
    transition: background-color 0.1s ease-in; }

.is-shown {
  visibility: visible;
  opacity: 1; }

.dark-mode-btn {
  width: 3rem;
  margin: 1rem;
  filter: invert(100%);
  transition: filter .1s ease-out; }
  .dark-mode-btn:hover {
    filter: invert(0%);
    cursor: pointer;
    transition: filter .1s ease-in; }

.main h2 {
  margin: 2rem 0; }

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
