.nosotros-div h4 {
  margin: 0 0 1rem 0; }

.nosotros-div p {
  margin: 0; }

@media (min-width: 768px) {
  .nosotros-div {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem; } }

.int-contacto h2 {
  margin-bottom: 0; }

.formulario fieldset {
  display: flex;
  flex-direction: column;
  border: 1px solid grey;
  margin: 0 0 2rem 0; }
  .formulario fieldset legend {
    padding: 0 1rem;
    font-weight: bold;
    font-size: 2rem; }
  .formulario fieldset label {
    font-weight: bold;
    text-transform: uppercase;
    display: block; }
  .formulario fieldset input, .formulario fieldset textarea {
    border: 1px solid #e1e1e1;
    padding: 1rem;
    margin-bottom: 1rem; }
  .formulario fieldset textarea {
    height: 10rem; }
  .formulario fieldset select {
    padding: 1rem;
    margin-bottom: 2rem; }
  .formulario fieldset p {
    margin: 0; }

.formulario input[type="submit"] {
  margin: 0rem auto 2rem auto;
  width: 15rem;
  height: 4rem;
  max-width: 80rem; }

.admin-msg {
  margin: 2rem 0;
  padding: 1rem;
  border-radius: 5px; }
  .admin-msg.admin-msg-error {
    background-color: #ff1f1f; }
  .admin-msg.admin-msg-success {
    background-color: #009900; }
  .admin-msg p {
    margin: 0;
    color: white; }

.propiedades {
  margin: 4rem 0 0;
  width: 100%;
  border-spacing: 0;
  text-align: center; }
  .propiedades thead {
    background-color: #009900; }
    .propiedades thead th {
      color: white;
      padding: 1rem; }
  .propiedades .propiedad-imagen {
    margin: 0 auto;
    min-width: 20rem;
    width: 100%;
    max-width: 40rem; }
  .propiedades tbody tr:nth-child(2n) {
    background-color: #dddddd; }
  .propiedades .propiedad-icon {
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center; }
    .propiedades .propiedad-icon img {
      width: 3rem; }
    .propiedades .propiedad-icon input[type="image"] {
      padding-top: .8rem;
      width: 2.5rem; }

.formulario-imagen {
  border: 1px solid #e1e1e1;
  padding: 1rem;
  max-width: 40rem;
  margin-bottom: 2rem; }
  .formulario-imagen img {
    width: 100%; }
  .formulario-imagen p {
    font-weight: bold;
    text-align: center; }

.dark-mode {
  background-color: #1a1a1a;
  transition: color .2s ease-in;
  transition: background-color .2s ease-in; }
  .dark-mode p, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode th, .dark-mode td, .dark-mode legend, .dark-mode label {
    color: white; }
  .dark-mode .anuncio {
    background-color: #333333;
    border: 1px solid black; }
    .dark-mode .anuncio .anuncio-contenido__precio {
      color: #71B100; }
    .dark-mode .anuncio .anuncio-icons img {
      filter: invert(100%); }
    .dark-mode .anuncio:hover {
      box-shadow: 0px 0px 10px 2px rgba(255, 255, 255, 0.3);
      -webkit-box-shadow: 0px 0px 10px 2px rgba(255, 255, 255, 0.3);
      -moz-box-shadow: 0px 0px 10px 2px rgba(255, 255, 255, 0.3); }
  .dark-mode .blog-entrada {
    background-color: #333333; }
  .dark-mode .formulario input:not([type="submit"]), .dark-mode .formulario textarea, .dark-mode .formulario option, .dark-mode .formulario select {
    color: white;
    background-color: #333333;
    border: 1px solid black; }
  .dark-mode .propiedades tbody tr:nth-child(2n) {
    background-color: #222222; }
  .dark-mode .propiedades .propiedad-icon img, .dark-mode .propiedades .propiedad-icon input[type="image"] {
    filter: invert(1); }
  .dark-mode .propiedades ._vendedor-tel {
    color: white; }
