.dark-mode
{
    background-color: darken($grisOscuro, 10);

    // Global
    p, h1, h2, h3, h4, th, td, legend, label 
    { 
        color: white;
    }

    // Anuncios
    .anuncio
    {
        background-color: $grisOscuro;
        border: 1px solid black;
        .anuncio-contenido__precio { color: $verde; }
        .anuncio-icons img { filter: invert(100%); }

        &:hover
        {
            box-shadow: 0px 0px 10px 2px rgba(255, 255, 255, 0.3);
            -webkit-box-shadow: 0px 0px 10px 2px rgba(255, 255, 255, 0.3);
            -moz-box-shadow: 0px 0px 10px 2px rgba(255, 255, 255, 0.3);
        }
    }

    // Blogs
    .blog-entrada
    {
        background-color: $grisOscuro;

    }
    .formulario
    {
        input:not([type="submit"]), textarea, option, select
        {
            color: white;
            background-color: $grisOscuro;
            border: 1px solid black;
        }
    }
    .propiedades
    {
        tbody
        {
            tr:nth-child(2n) { background-color: #222222; }
        }
        .propiedad-icon
        {
            img, input[type="image"] { filter: invert(1); }
        }
        ._vendedor-tel
        {
            color: white;
        }
    }

    transition: color .2s ease-in;
    transition: background-color .2s ease-in;
}