.webp .contacto { background-image: url("../img/encuentra.webp"); }
.no-webp .contacto { background-image: url("../img/encuentra.jpg"); }
.contacto
{
    background-position: center center;
    background-size: cover;
    height: 30rem;  

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center; // Centra todos los items
    padding: 1rem;

    color: white;
    h2, p, a { margin: 0; }
}