.anuncios
{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.anuncio
{
    display: grid;
    grid-template-columns: 1fr 3fr;

    position: relative;
    background-color: $gris;
    border: 1px solid darken($gris, 10);
    border-radius: 10px;

    min-width: 80rem;
    min-height: 24rem;
    max-height: 24rem;
    overflow: hidden;
    transition: box-shadow 0.1s ease-in;

    .anuncio-link
    {
        position: absolute;
        width: 100%;
        height: 100%;
    }
    .anuncio-img
    {
        min-width: 32rem;
    }

    &:hover
    {
        box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.3);
        -webkit-box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.3);
        -moz-box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.3);
        transition: box-shadow 0.1s ease-out;
    }
}


.anuncio-contenido
{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 1.5rem;

    h4, p
    { 
        margin: 0;
        word-break: break-word;
        text-align: left;
    }
    p { font-size: 1.8rem; }
    &__precio 
    {
        font-size: 2.4rem;
        color: $verde;
        font-weight: bold;
    }

    &__boton
    {
        display: block;
    }
}
.anuncio-icons
{
    display: flex;
    justify-content: center;
    list-style: none;

    li
    {
        display: flex; // El <img> y <p> se muestran al ladoflex: 1; // Cada li toma todo el espacio disponible - cada uno recibe 1px proporcional
        flex: 1; // Cada li toma todo el espacio disponible - cada uno recibe 1px proporcional
        gap: 1rem; // Separacion entre el <img> y <p>

        img { flex: 0 0 1rem; }
    }
    p { font-weight: bold; }
}

.anuncio-vertodas
{
    display: flex;
    justify-content: flex-end;
    margin-top: 2rem;
    margin-bottom: 5rem;
    a { width: auto; }
}

.anuncio-single
{
    h1, p 
    { 
        word-break: break-word;
    }

    &__precio
    {
        font-size: 2rem;
        font-weight: bold;
        color: $verde;
    }
}

